.weather-container {
   margin: 0 auto;
   margin-top: 5rem;
   @media (max-width: 768px) {
   }
}

.weather-main {
   border-radius: 30px;
   box-shadow: var(--boxShadow);
   padding: 2rem 1rem;
   margin-bottom: 2rem;
   background: #2c394b;
   color: #a2d9d9;
}
.weather {
   // .weather__data

   &__data {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media (max-width: 567px) {
         gap: 0.3rem;
      }
   }

   // .weather__more

   &__more {
      display: flex;
      column-gap: 2rem;
      font-size: 60px;
      flex-wrap: wrap;
      align-items: center;
      flex-basis: 50%;
      @media (max-width: 567px) {
         flex-basis: 100%;

         font-size: 40px;
         justify-content: space-between;
      }
   }

   // .weather__icon

   &__icon {
   }

   // .weather__temp

   &__temp {
      display: flex;
      align-items: flex-end;
      flex-direction: column;
      > div {
         display: flex;
         align-items: flex-start;
      }
      span {
         font-size: 16px;
      }
      @media (max-width: 567px) {
         order: -1;
         align-items: flex-start;
      }
   }

   // .weather__list

   &__list {
      flex: 0 0 100%;
      padding-left: 0;
      list-style: none;
      font-size: 16px;
      @media (max-width: 567px) {
         flex: 1 1 auto;
         margin-left: auto;
         font-size: 14px;
      }
   }

   // .weather__info

   &__info {
      text-align: right;
      @media (max-width: 567px) {
         flex: 1;
         font-size: 14px;
      }
   }

   // .weather__city

   &__city {
      margin-bottom: 1rem;
      font-weight: 700;
      font-size: 25px;
      letter-spacing: 0.3rem;
      @media (max-width: 567px) {
         font-size: 22px;
         margin-bottom: 0.4rem;
      }
   }
}

.weather__list-card {
   margin-top: 2rem;
   display: flex;
   gap: 1rem;
   justify-content: space-evenly;
   @media (max-width: 600px) {
      margin-top: 0.5rem;
      gap: 5px;
   }
   @media (max-width: 400px) {
      flex-wrap: wrap;
      justify-content: center;
   }
}
