.search {
}

.search-form {
   margin: 0 auto;
   display: flex;
   input {
      padding: 0.7rem 1rem;
      border-radius: 20px 0 0 20px;
      outline: none;
      border: none;
      flex: 0 1 90%;
      background: #d3d2d9;
      &:focus {
         box-shadow: var(--boxShadow);
      }
      @media (max-width: 567px) {
         padding: 0.4rem 1rem;
      }
   }
   button {
      border-radius: 0 20px 20px 0;
      outline: none;
      border: none;
      flex: 1 1 120px;
      cursor: pointer;
      background-color: var(--gray);
      transition: all 0.3s ease 0s;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      &:hover {
         background-color: var(--black);
         color: var(--glass);
      }
      &:focus {
         box-shadow: var(--boxShadow);
         outline: 1px solid black;
      }
   }
}
